.passwordRecoveryContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 25px 25px 70px;
    max-width: 415px;
    width: 100%;
    border-radius: 10px;
    background-color: #F9F9FE;
    margin: 10px auto 0;
}

h4 {
    font-size: 30px;
}

.inputField {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 84px;
}

.inputField p {
    font-size: 14px;
}

